li {
    list-style-type: none !important;
}

.rct-collapse-btn {
    margin-right: 8px;
    height: 0.7rem;
    width: 0.7rem;
    position: relative;
    border: 5px;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    background-color: #36c8d8 !important;
}

.rct-title {
    margin-left: 5px;
}

.rct-node-expanded .rct-collapse-btn {
    background-image: url(dash.svg);
    background-repeat: no-repeat;
    background-position: center center;
}

.rct-node-expanded .rct-collapse-btn:hover {
    background-color: #ff4d4d !important;
    transition: 0.2s;
}

.rct-node-collapsed .rct-collapse-btn {
    background-image: url(plus.svg);
    background-repeat: no-repeat;
    background-position: center center;
}

.rct-node-collapsed .rct-collapse-btn:hover {
    background-color: #25dd2b !important;
    transition: 0.2s;
}