.masthead {
    text-align: center;
    height: 8%;
    width: 100%;
    background-color: #bbbbbbbb;
    position: fixed;
    /* Fixed Sidebar (stay in place on scroll) */
    z-index: 1;
    /* Stay on top */
    overflow-x: hidden;
    overflow-y: hidden;
    /* Disable horizontal scroll */
    padding-top: 0px;
    top: 0;
    /* Stay at the top */
    left: 0;
    box-shadow: 0.5px 0.5px 3px #bbb;
}

.masthead .research-button {
    position: absolute;
    left: 0;
    width: 10%;
    top: 50%;
    transform: translateY(-50%);
}

.masthead .new-key-word-bar {
    flex-grow:1;
    text-align:center;
}

.cours {
    height: 70%;
    /* Full-height: remove this if you want "auto" height */
    width: 30%;
    /* Set the width of the sidebar */
    position: fixed;
    /* Fixed Sidebar (stay in place on scroll) */
    z-index: 1;
    /* Stay on top */
    top: 8%;
    /* Stay at the top */
    left: 50%;
    background-color: #ffabab;
    /* Black */
    overflow-x: hidden;
    /* Disable horizontal scroll */
    padding-top: 20px;
    padding-left: 50px;
}

.barre-laterale {
    height: 92%;
    /* Full-height: remove this if you want "auto" height */
    width: 23%;
    /* Set the width of the sidebar */
    position: fixed;
    /* Fixed Sidebar (stay in place on scroll) */
    z-index: 1;
    /* Stay on top */
    top: 8%;
    /* Stay at the top */
    left: 0%;
    background-color: #e0dfde;
    /* Black */
    overflow-x: hidden;
    /* Disable horizontal scroll */
    font-family: 'Roboto', 'Noto', sans-serif;
    font-weight: 400;
    margin-left: -30px;
    box-shadow: 0.5px 0.5px 3px #bbb;
}

.frame-results {
    height: 92%;
    width: 77%;
    position: fixed;
    z-index: 1;
    top: 8%;
    left: 23%;
    overflow-x: hidden;
    padding: 0px 20px;
}

.boutton-rechercher {
    background-color: #ff8800;
    color: rgb(252, 252, 252);
    border: 5px;
    font-weight: 400;
}

.boutton-rechercher:hover {
    background-color: #429628;
    color: rgb(252, 252, 252);
    transition: 0.2s;
}
